import styles from "./login.module.css";
import { Button } from "@mui/material";
import { NavLink,json,useNavigate} from "react-router-dom";
import {useEffect, useState, useRef} from "react";
const Login = () => {
    const navigate = useNavigate()
    useEffect(()=>{ 
        const user_id = localStorage.getItem("user_id");
    if(user_id){
        navigate("/");
    }},[]);
    const [email,setEmail] = useState("")
    const [password,setPassword]=useState("")
    const warning=useRef();
    let users = []
    if(JSON.parse(localStorage.getItem("users")) != null){
        users = JSON.parse(localStorage.getItem("users"))
        console.log(users)
    }
    function login (e){
        e.preventDefault();
        let User = users.filter((user)=>{
            return user.email == email && user.password == password
        })
        if(User.length == 1){
            const loggedUser_id = User[0].user_id
            localStorage.setItem('user_id', loggedUser_id);
            navigate("/")
        }
        else{
            warning.current.styles.display = "block"
        }
    }
    return ( 
        <>
        <div className={styles.container}>
            <form class={styles.form}>
                <h2>Login</h2>
                <input id={styles.email} type="text" placeholder="Enter Your Email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} />
                <input id={styles.pass} type="password" placeholder="Enter Your Password" onChange={(e)=>{
                    setPassword(e.target.value)
                }} />
                <p ref={warning} className={styles.warning}>Wrong Password OR Email</p>
                <div className={styles.check}>
                <div className={styles.remember}>
                <input type="checkbox"/>
                <label>Remember me</label>
                </div>
                <div className={styles.forgot}>
                    <NavLink>Forgot Password?</NavLink>
                </div>
                </div>
                <Button id={styles.signup} variant="contained" onClick={login}>LOG iN</Button>
                <p className={styles.line}>OR</p>
                <div className={styles.buttons}>
                <Button id={styles.facebook} variant="contained">Facebook</Button>
                <Button id={styles.google} variant="contained">Google</Button>
                </div>
                <h3>Don't Have an Account? <NavLink to="/signup">Sign up now</NavLink></h3>
            </form>
        </div>
        </>
    );
}

export default Login;