import {useEffect} from "react";
import {NavLink, useNavigate} from "react-router-dom";
// import Container from 'react-bootstrap/Container';
// import Nav from 'react-bootstrap/Nav';
// import Navbar from 'react-bootstrap/Navbar';
// import NavDropdown from 'react-bootstrap/NavDropdown';
import 'bootstrap/dist/css/bootstrap.min.css';
import styles from "./home.module.css";
import { Button} from "react-bootstrap";
import { Dropdown } from "react-bootstrap";
import england from '../images/eng.png';
import france from '../images/fn.png'
import us from '../images/us.png'
import logo from '../images/logo_dark.png'
import PhoneIphoneIcon from '@mui/icons-material/PhoneIphone';
import ShuffleIcon from '@mui/icons-material/Shuffle';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import PersonIcon from '@mui/icons-material/Person';
import SearchIcon from '@mui/icons-material/Search';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import Carousel from 'react-bootstrap/Carousel';
import banner1 from '../images/banner1.jpg';
import banner2 from '../images/banner2.jpg';
import banner3 from '../images/banner3.jpg';
import shop_banner1 from '../images/shop_banner_img1.jpg'
import shop_banner2 from '../images/shop_banner_img2.jpg'

const Home = () => {
    const navigate = useNavigate()
    useEffect(()=>{ 
        const user_id = localStorage.getItem("user_id");
    if(!user_id){
        navigate("/login");
    }},[]);
    return ( 
      <>
      <header>
      <div className={styles.topNav}>
          <div className={styles.topLeft}>
            <Dropdown className={styles.country}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}><img src={england} alt="eng" className={styles.england}></img>English</Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCountry}>
                <Dropdown.Item href="#/action-1"><img src={england} alt="eng" className={styles.england}></img>English</Dropdown.Item>
                <Dropdown.Item href="#/action-2"><img src={france} alt="eng" className={styles.england}></img>France</Dropdown.Item>
                <Dropdown.Item href="#/action-3"><img src={us} alt="eng" className={styles.england}></img>United States</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Dropdown className={styles.currency}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}>USD</Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCurrency}>
                <Dropdown.Item href="#/action-1" className={styles.dropCurrency}>USD</Dropdown.Item>
                <Dropdown.Item href="#/action-2" className={styles.dropCurrency}>EUR</Dropdown.Item>
                <Dropdown.Item href="#/action-3" className={styles.dropCurrency}>GBR</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <h1 className={styles.phone}><PhoneIphoneIcon/></h1>
            <p className={styles.number}>123-456-7890</p>
          </div>
          <div className={styles.topRight}>
          <h1 className={styles.shuffle}><ShuffleIcon/></h1>
          <NavLink className={styles.compare}>Compare</NavLink>
          <h1 className={styles.heart}><FavoriteBorderIcon/></h1>
          <NavLink className={styles.wishlist}>Wishlist</NavLink>
          <h1 className={styles.person}><PersonIcon/></h1>
          <NavLink className={styles.loginn}>Wishlist</NavLink>
          </div>
        </div>
        <div className={styles.bottomNav}>
          <div className={styles.bottomLeft}>
            <img src={logo} alt="logo" className={styles.logo}/>
          </div>
          <div className={styles.bottomRight}>
            <div className={styles.dropDowns}>
            <Dropdown className={styles.home}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}><NavLink to="/home">Home</NavLink></Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCountry}>
                <Dropdown.Item href="#/action-1">Fashion 1</Dropdown.Item>
                <Dropdown.Item href="#/action-2">Fashion 2</Dropdown.Item>
                <Dropdown.Item href="#/action-3">Furniture 1</Dropdown.Item>
                <Dropdown.Item href="#/action-3">Furniture 2</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Dropdown className={styles.pages}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}>Pages</Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCountry}>
                <Dropdown.Item href="#/action-1">About Us</Dropdown.Item>
                <Dropdown.Item href="#/action-2">Contact Us</Dropdown.Item>
                <Dropdown.Item href="#/action-3">Faq</Dropdown.Item>
                <Dropdown.Item href="#/action-3">Terms And Conditions</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            <Dropdown className={styles.products}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}><NavLink to="/products">Products</NavLink></Dropdown.Toggle>
              <Dropdown.Menu className={styles.drooop}>
              <div className={styles.drop}>
                <div>
                <Dropdown.Item href="#/action-1">Woman's</Dropdown.Item>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
                </div>
                <div>
                <Dropdown.Item href="#/action-1">Woman's</Dropdown.Item>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
                </div>
                <div>
                <Dropdown.Item href="#/action-1">Woman's</Dropdown.Item>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
                </div>
                <div>
                <Dropdown.Item href="#/action-1">Woman's</Dropdown.Item>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
                </div>
                </div>
              </Dropdown.Menu>
              
            </Dropdown>
            <Dropdown className={styles.blog}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}><NavLink to="/cart">Cart</NavLink></Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCountry}>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
      
            <Dropdown className={styles.contactUs}>
              <Dropdown.Toggle variant="secondary" id={styles.dropdown}><NavLink to="/cart">About</NavLink></Dropdown.Toggle>
              <Dropdown.Menu className={styles.dropCountry}>
                <Dropdown.Item href="#/action-1">English</Dropdown.Item>
                <Dropdown.Item href="#/action-2">France</Dropdown.Item>
                <Dropdown.Item href="#/action-3">United States</Dropdown.Item>
              </Dropdown.Menu>
            </Dropdown>
            </div>
            <h1 className={styles.search}><SearchIcon/></h1>
            <h1 className={styles.Cart}><ShoppingCartOutlinedIcon/></h1>
          </div>
        </div>
      </header>
        <div className={styles.banner}>
        <Carousel className={styles.carousel}>
      <Carousel.Item>
      <img src={banner1} alt="eng" className={styles.banner1}></img>
        <Carousel.Caption className={styles.caption}>
          <h1 className={styles.headerBanner1}>Woman Fashion</h1>
          <Button className={styles.btn}>SHOP NOW</Button>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
      <img src={banner2} alt="eng" className={styles.banner2}></img>
        <Carousel.Caption className={styles.caption}>
          <p className={styles.paraBanner2}>50% off in all products</p>
          <h1 className={styles.headerBanner2}>Man Fashion</h1>
          <Button className={styles.btn}>SHOP NOW</Button>
        </Carousel.Caption>
      </Carousel.Item>
      <Carousel.Item>
      <img src={banner3} alt="eng" className={styles.banner3}></img>
        <Carousel.Caption className={styles.caption}>
        <p className={styles.paraBanner3}>Taking Your Viewing Experience  to Next Level</p>
          <h1 className={styles.headerBanner3}>Summer Sale</h1>
          <Button className={styles.btn}>SHOP NOW</Button>
        </Carousel.Caption>
      </Carousel.Item>
    </Carousel>
        </div>
        <div className={styles.shop}>
          <div className={styles.left}>
            <img src={shop_banner1} alt="eng" className={styles.shop_banner1}></img>
            <p>Super Sale</p>
            <h3>New Collection</h3>
            <NavLink className={styles.navl}>Shop Now</NavLink>
          </div>
          <div className={styles.right}>
            <img src={shop_banner2} alt="eng" className={styles.shop_banner2}></img>
            <h3>New Collection</h3>
            <p>Super Sale</p>
            <NavLink className={styles.navl}>Shop Now</NavLink>
          </div>
        </div>
        <h1 className={styles.exc_prod}>Exclusive Products</h1>
        <div className={styles.links}>
          <div className={styles.new}><NavLink>New Arrival</NavLink></div>
          <div className={styles.best}><NavLink>Best Seller</NavLink></div>
          <div className={styles.featured}><NavLink>Featured</NavLink></div>
          <div className={styles.special}><NavLink>Special Offer</NavLink></div>
        </div>
      </>
    );
}

export default Home;